import s from "./styles.css"
import { useVirtualList } from "~/components/Hooks/useVirtualList"
export const links = [{ rel: "stylesheet", href: s }]

export const Items = ({ children, service, items }) => {
  const [isLoading, rowVirtualizer] = useVirtualList({ service, items })

  return (
    <div
      style={{
        position: "relative",
      }}
    >
      {/* {items.length == 0 && <NotFound title="Nenhum Evento Encontrado" />} */}
      <div
        className="List"
        style={{
          height: "100%",
          width: `100%`,
          overflow: "auto",
        }}
      >
        <div
          style={{
            height: `${rowVirtualizer.totalSize}px`,
            width: "100%",
            position: "relative",
          }}
        >
          {rowVirtualizer.virtualItems.map((virtualRow) => {
            const isLoaderRow = virtualRow.index > items.length - 1
            const events = items[virtualRow.index] || []

            return (
              <div
                key={virtualRow.index}
                style={{
                  position: "absolute",
                  top: 0,
                  left: 0,
                  width: "100%",
                  height: `${virtualRow.size}px`,
                  transform: `translateY(${virtualRow.start}px)`,
                }}
              >
                <Box row>
                  <Box row className={s.items}>
                    teste
                    {children}
                  </Box>

                  {/* {isLoaderRow && isLoading && (
                    <Box
                      flexDirection="row"
                      flex={1}
                      className={s.list}
                      alignItems="center"
                      justifyContent="center"
                    >
                      <Spinner />
                    </Box>
                  )} */}
                </Box>
              </div>
            )
          })}
        </div>
      </div>
    </div>
  )
}

Items.links = links
export default Items
